import { useState, useCallback, useEffect } from "react";
import { useSynth } from "hooks/useSynth";
import { beginnerNotes } from "helpers/notes";
import { useInterval } from "hooks/useInterval";
import {
  useNoteDetectionHistory,
  PitchDetectionValue,
} from "hooks/useNoteDetectionHistory";

export const EarTrainingPage = () => {
  const [detectionHistory, setDetectionHistory] = useState<
    PitchDetectionValue[]
  >([]);
  const { sampler } = useSynth({ instrument: "whistle" });

  const [notes, setNotes] = useState<string[]>([]);
  const [reveal, setReveal] = useState(false);
  const { clearHistory: clearNoteDetectionHistory } = useNoteDetectionHistory(
    (h, p) => {
      if (!notes.includes(p.note.name)) return;

      setDetectionHistory(h);
    }
  );

  const playNotes = useCallback(() => {
    notes.forEach((val, i) => {
      sampler?.triggerAttackRelease(notes[i], "8n", 0.5 * i + sampler?.now());
    });
  }, [notes, sampler]);

  const { reset: resetInterval } = useInterval(playNotes, 2000);

  const reset = () => {
    let arr = [getRandomNote(), getRandomNote(), getRandomNote()];
    setNotes(arr);
    clearNoteDetectionHistory();
  };

  useEffect(() => {
    playNotes();
    resetInterval();
  }, [notes, playNotes, resetInterval]);

  const detectedNotesString = uniqueInOrder(
    detectionHistory
      .filter((n) => notes.includes(n.note.name))
      .map((n) => n.note.name)
  ).join(",");

  const notesString = uniqueInOrder([...notes].reverse()).join(",");
  console.log({ notes });
  if (detectedNotesString.includes(notesString)) {
    reset();
  }

  return (
    <div>
      <div>
        {!reveal && (
          <button
            style={{ width: 300, marginBottom: 6 }}
            onClick={() => {
              setReveal(true);
            }}
          >
            Reveal
          </button>
        )}
        {reveal && (
          <h1
            onClick={() => {
              setReveal(false);
            }}
          >
            {notes.join(", ")}
          </h1>
        )}
      </div>
      <div>
        <button
          style={{ width: 300 }}
          onClick={() => {
            reset();
          }}
        >
          Start/Reset
        </button>
      </div>{" "}
      <div style={{ width: 300, marginTop: 6 }}>
        {detectionHistory
          .map((n) => n.note.name)
          .slice(0, 9)
          .join(", ")}
      </div>
    </div>
  );
};

const getRandomNote = () => {
  return beginnerNotes[Math.floor(Math.random() * beginnerNotes.length)];
};

function uniqueInOrder(iterable: any) {
  let strArr = Array.isArray(iterable) ? iterable : iterable.split("");
  let unique = strArr.filter((letter: any, i: any) => {
    return strArr[i] !== strArr[i + 1];
  });

  return unique;
}
