import { Note } from "@tonaljs/core";

export enum FlashcardType {
  NoteName = "Name",
  SheetMusicNote = "Sheet Music",
  Sound = "Sound",
}

export enum FlashcardSource {
  Notes = "Random Notes",
  Songs = "Songs",
}

export interface Flashcard {
  type: FlashcardType;
  note: Note;
  id: string;
}

export type InstrumentType = "accordion" | "flute" | "violin" | "whistle";
