import React, { useContext } from "react";

import { note, Note, NoNote } from "@tonaljs/core";

interface ProviderState {
  baseNote: Note | NoNote;

  showSheetMusicKeySignature: boolean;
}

interface ProviderStore {
  state: ProviderState;
  update: (arg: ProviderState) => void;
}

const Context = React.createContext({} as ProviderStore);
export const useAppSettings = () => useContext(Context);

export class AppSettingsProvider extends React.Component<{}, ProviderState> {
  public readonly state = {
    baseNote: note("D5"),
    showSheetMusicKeySignature: false,
  };

  private update = (state: ProviderState) => {
    this.setState(state);
  };

  public render() {
    const store: ProviderStore = {
      state: this.state,
      update: this.update,
    };

    return (
      <Context.Provider value={store}>{this.props.children}</Context.Provider>
    );
  }
}
