import { useState } from "react";
import { Flashcards } from "components/Flashcards/Flashcards";
import Select from "react-select";
import abcjs from "abcjs";
import { FlashcardType, FlashcardSource, Flashcard } from "types/Flashcard";
import { beginnerNotes, isNote, tuneBookString } from "helpers/notes";
import { note as getNote } from "@tonaljs/core";
import * as NoteFunctions from "@tonaljs/note";

export const GamePage = () => {
  const [gameFlashcards, setGameFlashcards] = useState<Flashcard[]>([]);

  const typeOptions = Object.values(FlashcardType).map((i) => {
    return { label: i, value: i };
  });
  const [types, setTypes] = useState(
    typeOptions.filter((v) => v.value === FlashcardType.Sound)
  );

  const sourceOptions = Object.values(FlashcardSource).map((i) => {
    return { label: i, value: i };
  });

  const [source, setSource] = useState(sourceOptions[0]);

  const startGame = () => {
    let flashcards: Flashcard[] = [];

    if (source.value === FlashcardSource.Notes) {
      for (let i of Array(100).keys()) {
        const note = getNote(
          beginnerNotes[Math.floor(Math.random() * beginnerNotes.length)]
        );

        if (!isNote(note)) return;

        const type = types[Math.floor(Math.random() * types.length)].value;

        flashcards.push({
          type,
          note,
          id: Math.random().toString(36).replace("0.", ""),
        });
      }
    }

    if (source.value === FlashcardSource.Songs) {
      const tuneBook = new abcjs.TuneBook(tuneBookString);
      //var measureArray = abcjs.extractMeasures(tuneBookString);

      const tuneObj = abcjs.parseOnly(tuneBook.tunes[0].abc);
      const audioCommands = tuneObj[0]?.setUpAudio({});

      const t = audioCommands.tracks[0] as any;

      t.forEach((c: any) => {
        if (c.cmd !== "note") return;

        const type = types[Math.floor(Math.random() * types.length)].value;
        const note = getNote(
          NoteFunctions.fromMidiSharps(c.pitch)
            .replace("5", "6")
            .replace("4", "5")
        );

        if (!isNote(note)) return;

        flashcards.push({
          type,
          note,
          id: Math.random().toString(36).replace("0.", ""),
        });
      });
    }

    setGameFlashcards(flashcards);
  };

  return (
    <div>
      {gameFlashcards.length === 0 && (
        <div style={{ minWidth: 300 }}>
          <div>
            <label style={{ marginBottom: 5, display: "block" }}>
              Note Source
            </label>
            <Select
              options={sourceOptions}
              value={source}
              onChange={(val) => setSource(val as any)}
              isMulti={false}
              isClearable={false}
              isSearchable={false}
            />
            <label style={{ marginBottom: 5, marginTop: 5, display: "block" }}>
              Card Types
            </label>
            <Select
              options={typeOptions}
              value={types}
              onChange={(val) => setTypes(val as any)}
              isMulti={true}
              isClearable={false}
              isSearchable={false}
            />{" "}
          </div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <button style={{ marginTop: 20 }} onClick={startGame}>
              Start
            </button>
          </div>
        </div>
      )}

      {gameFlashcards.length !== 0 && (
        <Flashcards flashcards={gameFlashcards} />
      )}
    </div>
  );
};
