import { useState, useEffect } from "react";
import { Sampler } from "tone";
import { Note } from "@tonaljs/core";
import { InstrumentType } from "types/Flashcard";

export const useSynth = ({ instrument }: { instrument: InstrumentType }) => {
  const [sampler, setSampler] = useState<Sampler | null>();

  useEffect(() => {
    const options = {
      onload: () => {
        setSampler(sampler);
      },
    };
    const sampler = new Sampler(
      {
        A4: `/tin-whistle-trainer/soundfonts/${instrument}-mp3/A4.mp3`,
        A5: `/tin-whistle-trainer/soundfonts/${instrument}-mp3/A5.mp3`,
        D6: `/tin-whistle-trainer/soundfonts/${instrument}-mp3/D6.mp3`,
      },
      options
    ).toDestination();
  }, [setSampler]);

  const playNote = (note: Note) => {
    sampler?.triggerAttackRelease(note.name, "2n", sampler?.now());
  };

  return { sampler, playNote };
};
