import { useState, useEffect } from "react";

export const useCountdown = (start: number) => {
  const [count, setCount] = useState(start);

  const reset = () => {
    setCount(start);
  };

  useEffect(() => {
    if (count === 0) {
      return;
    }
    const timer = setTimeout(() => {
      setCount(count - 1);
    }, 1000);

    return () => {
      clearTimeout(timer);
    };
  }, [count]);

  return { count, reset, set: setCount };
};
