import { useCallback, useRef } from "react";
import { useAudioFeatures } from "./useAudioFeatures";
import { NoNote, Note, note as getNote } from "@tonaljs/core";

export interface PitchDetectionValue {
  note: Note | NoNote;
  createdAt: Date;
  id: string;
  duration: number;
}

const getDefaultPitchDetectionValue: () => PitchDetectionValue = () => {
  return {
    note: getNote("NoNote"),
    createdAt: new Date(),
    id: "init",
    duration: 0,
  };
};

export const useNoteDetectionHistory = (
  cb: (
    history: PitchDetectionValue[],
    lastDetected: PitchDetectionValue
  ) => void
) => {
  const cbRef = useRef(cb);
  cbRef.current = cb;

  const history = useRef<PitchDetectionValue[]>([
    getDefaultPitchDetectionValue(),
  ]);

  useAudioFeatures((f) => {
    const newItem: PitchDetectionValue = {
      note: f.noteFromPitch,
      id: f.id,
      createdAt: new Date(),
      duration: 0,
    };

    const historyFirst = history.current[0];

    const timeElapsed = Math.abs(
      new Date().getTime() - historyFirst.createdAt.getTime()
    );

    const hasTimeElapsed = Math.abs(timeElapsed - historyFirst.duration) > 100;
    const hasNewNote = historyFirst.note?.name !== newItem.note.name;

    if (!hasTimeElapsed && !hasNewNote) return;

    const update = [...history.current.slice(0, 100)];

    update[0] = {
      ...update[0],
      duration: timeElapsed,
    };

    if (hasNewNote) {
      update.unshift(newItem);
    }

    history.current = update;
    cbRef.current(history.current, newItem);
  });

  const clearHistory = useCallback(() => {
    const p = getDefaultPitchDetectionValue();
    history.current = [p];
    cbRef.current(history.current, p);
  }, [history]);

  return {
    clearHistory,
    history,
  };
};
