import { useEffect } from "react";
import { Note } from "@tonaljs/core";
import { useSynth } from "hooks/useSynth";

export const PlayNoteButton = ({ note }: { note: Note }) => {
  const { playNote } = useSynth({ instrument: "whistle" });

  return (
    <button
      onClick={() => {
        playNote(note);
      }}
    >
      ▶ Play
    </button>
  );
};
