import { useState, useEffect } from "react";
import { useAppSettings } from "contexts/AppSettingsContext";
import { Link } from "react-router-dom";
import { useTone } from "hooks/useTone";

export const SettingsPage = () => {
  const [devices, setDevices] = useState<MediaDeviceInfo[]>([]);
  const { state, update } = useAppSettings();
  const { baseNote, showSheetMusicKeySignature } = state;
  const { changeAudioDevice, audioDeviceId } = useTone();

  useEffect(() => {
    navigator.mediaDevices.enumerateDevices().then((devices) => {
      setDevices(devices.filter((d) => d.kind === "audioinput"));
    });
  }, []);

  return (
    <div>
      <h1>Setting</h1>
      <p>Base Note: {baseNote.name}</p>
      <div style={{ marginBottom: 12 }}>
        {devices.length > 0 && (
          <select
            value={audioDeviceId || ""}
            onChange={(event: React.ChangeEvent<HTMLSelectElement>) => {
              changeAudioDevice(event.target.value);
            }}
            style={{ maxWidth: 300 }}
          >
            <option>No Device Selected</option>
            {devices.map((d) => (
              <option key={d.deviceId} value={d.deviceId}>
                {d.label}
              </option>
            ))}
          </select>
        )}
      </div>
      <div style={{ marginBottom: 12 }}>
        <label>
          <input
            onChange={(e) => {
              update({
                ...state,
                showSheetMusicKeySignature: !showSheetMusicKeySignature,
              });
            }}
            type="checkbox"
            checked={showSheetMusicKeySignature}
          />
          Use key signature for sheet music
        </label>
      </div>
      <div>
        <Link to="/test">Test Page</Link> <br />
        <Link to="/notes">Notes</Link>
      </div>
    </div>
  );
};
