import { useEffect, useRef } from "react";

import * as Tone from "tone";
import { useTone } from "hooks/useTone";
import useAnimationFrame from "use-animation-frame";

import { NoNote, Note, note as getNote } from "@tonaljs/core";
import { fromFreqSharps } from "@tonaljs/note";

import * as Pitchfinder from "pitchfinder";

export interface AudioFeatures {
  id: string;
  pitch: number | null;
  noteFromPitch: Note | NoNote;
}

export const getDefaultAudioFeature = () => {
  return { id: "", pitch: 0, noteFromPitch: getNote("NoNote") };
};

export function useAudioFeatures(cb: (prop: AudioFeatures) => void) {
  const tone = useTone();

  const cbRef = useRef(cb);

  useEffect(() => {
    cbRef.current = cb;
  }, [cb]);

  useAnimationFrame(() => {
    if (Tone.context.state !== "running") return;

    const detectPitch = Pitchfinder.ACF2PLUS({
      sampleRate: Tone.context.sampleRate,
      //minFrequency: 73,
      //maxFrequency: 4699,
      //ratio: 5,
      //sensitivity: 0.1,
    });

    const id = Math.random().toString(36).replace("0.", "");
    const pitch = detectPitch(tone.analyser.getValue() as Float32Array);
    const pitchWithinBounds = pitch && pitch < 4699 && pitch > 73;

    const noteFromPitch = getNote(
      pitchWithinBounds ? fromFreqSharps(pitch) : "NoNote"
    );

    cbRef.current({
      id,
      pitch: pitchWithinBounds ? pitch : null,
      noteFromPitch,
    });
  }, [tone]);
}
