import { useState } from "react";
import { note as getNote } from "@tonaljs/core";
import { beginnerNotes, isNote } from "helpers/notes";
import { NoteCard } from "components/NoteCard/NoteCard";

export const NotesPage = () => {
  const [index, setIndex] = useState(0);

  const [cardsData] = useState(beginnerNotes.map((note) => getNote(note)));

  if (index >= cardsData.length) setIndex(0);
  if (index < 0) setIndex(cardsData.length - 1);

  const card = cardsData[index];

  return (
    <div>
      <div>{card && isNote(card) && <NoteCard note={card} />}</div>

      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <button onClick={() => setIndex(index - 1)}> {"<<"} </button>
        <button onClick={() => setIndex(index + 1)}> {">>"} </button>
      </div>
    </div>
  );
};
