import tunerIconSVG from "assets/icons/tuner.svg";
import gearIconSVG from "assets/icons/gear.svg";
import whistleIconSVG from "assets/icons/whistle.svg";
import earIconSVG from "assets/icons/ear.svg";

import { useTone } from "hooks/useTone";

import { Outlet, Link } from "react-router-dom";

function App(props: any) {
  const start = useTone((s) => s.start);
  const contextState = useTone((s) => s.contextState);
  const isReady = contextState === "running";

  return (
    <div className="App" onClick={start}>
      <main>
        {isReady && <Outlet />}
        {!isReady && <button>Allow Microphone and Audio permissions</button>}
      </main>
      <header>
        <Link to="/">
          <img alt="" src={whistleIconSVG} />
          Play
        </Link>
        <Link to="/ear-training">
          <img alt="" src={earIconSVG} />
          Ear Training
        </Link>

        <Link to="/tuner">
          <img alt="" src={tunerIconSVG} />
          Tuner
        </Link>
        <Link to="/settings">
          <img alt="" src={gearIconSVG} />
          Settings
        </Link>
      </header>
    </div>
  );
}

export default App;
