import { useState } from "react";

import {
  useAudioFeatures,
  AudioFeatures,
  getDefaultAudioFeature,
} from "hooks/useAudioFeatures";

import { Tab } from "components/Tab/Tab";
import AudioVisualizer from "components/AudioVisualizer/AudioVisualizer";

import { MusicNote } from "components/MusicNote/MusicNote";

export const TunerPage = () => {
  const [audioFeature, setAudioFeature] = useState<AudioFeatures>(
    getDefaultAudioFeature()
  );

  useAudioFeatures((f) => {
    setAudioFeature(f);
  });

  const note = audioFeature.noteFromPitch;

  return (
    <div>
      <div style={{ textAlign: "center", minHeight: 30 }}></div>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          width: "95vw",
          marginBottom: 5,
        }}
      >
        <div style={{ width: 100 }}>
          <h1>{audioFeature.noteFromPitch.name}</h1>
        </div>
        <div>
          <AudioVisualizer style={{ width: 60, height: 60 }} />
        </div>
        <div style={{ textAlign: "right", width: 140 }}>
          {audioFeature.pitch && <h1>{Math.round(audioFeature.pitch)}㎐</h1>}
        </div>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "95vw",
        }}
      >
        <div
          style={{
            height: 30 * 7,
            width: 30,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {!note.empty && (
            <Tab note={note} style={{ height: 30 * 7, width: 30 }} />
          )}
        </div>
        <div style={{ width: 120 }}>
          {!note.empty && <MusicNote note={note} />}
        </div>
      </div>
    </div>
  );
};
