import { useState } from "react";

import { useAudioFeatures, AudioFeatures } from "hooks/useAudioFeatures";

import {
  useNoteDetectionHistory,
  PitchDetectionValue,
} from "hooks/useNoteDetectionHistory";

export const TestPage = () => {
  const [history, setHistory] = useState<AudioFeatures[]>([]);

  const [detectionHistory, setDetectionHistory] = useState<
    PitchDetectionValue[]
  >([]);

  const { clearHistory } = useNoteDetectionHistory((h) => {
    setDetectionHistory(h);
  });

  useAudioFeatures((f) => {
    setHistory([f, ...history.slice(0, 90)]);
  });

  return (
    <div>
      <div>
        <button
          onClick={() => {
            //pause();
          }}
        >
          Pause
        </button>
        <button
          onClick={() => {
            //resume();
          }}
        >
          Play
        </button>
        <button
          onClick={() => {
            clearHistory();
          }}
        >
          Clear History
        </button>
      </div>

      <div>
        {detectionHistory
          .slice(0, 6)
          .map((h) => `${h.note.name} [${h.duration}]`)
          .join(",")}
      </div>
      <div style={{ display: "flex" }}>
        {history.map((h) => (
          <div
            key={h.id}
            className={"thicc"}
            style={{
              width: "1vw",
              overflow: "hidden",
              border: "1px solid black",
            }}
          >
            <div>{(h.pitch || 0).toFixed(3)}</div>
            <div>{h.noteFromPitch.name || "#"}</div>
          </div>
        ))}
      </div>
    </div>
  );
};
