import { useAppSettings } from "contexts/AppSettingsContext";
import { NoNote, Note } from "@tonaljs/core";

export const Tab = ({
  note,
  style,
}: {
  note: Note;
  style?: React.CSSProperties;
}) => {
  const {
    state: { baseNote },
  } = useAppSettings();

  const fingering = getFingerings(note, baseNote);
  const segments = 7;
  const gutter = 24;
  const strokeWidth = 10;
  const plusLineWidth = 20;

  if (!fingering || note.empty) return <></>;

  return (
    <svg
      style={{ background: "#eee", ...style }}
      width="300"
      height="500"
      viewBox={`0 0 100 ${100 * segments + (segments - 1) * gutter}`}
    >
      <defs>
        <linearGradient id="half">
          <stop offset="0%" stopColor="black" />
          <stop offset="50%" stopColor="black" />
          <stop offset="50%" stopColor="transparent" />
          <stop offset="100%" stopColor="transparent" />
        </linearGradient>
      </defs>

      <rect x="0" y="0" width="100%" height="100%" fill="#ddd" />
      <g strokeWidth={strokeWidth} stroke="black">
        {[...Array(segments)].map((e, i) => {
          const y = i * 100 + 50 + i * gutter;

          return (
            <g key={i}>
              {i !== segments - 1 ? (
                <circle
                  key={i}
                  cx="50"
                  cy={y}
                  r="45"
                  fill={
                    { o: "transparent", x: "black", "-": "black" }[
                      fingering.split("")[i]?.toLowerCase()
                    ] || "url(#half)"
                  }
                />
              ) : (
                <g>
                  {fingering.endsWith("+") && (
                    <>
                      <rect
                        x={50 - plusLineWidth / 2}
                        y={y - 50 + strokeWidth / 2}
                        fill="black"
                        height={100 - strokeWidth}
                        width={plusLineWidth}
                      />
                      <rect
                        width={100 - strokeWidth}
                        height={plusLineWidth}
                        x={0 + strokeWidth / 2}
                        y={y - strokeWidth}
                        fill="black"
                      />
                    </>
                  )}
                </g>
              )}
            </g>
          );
        })}
      </g>
    </svg>
  );
};

const FINGERINGS = [
  "------",
  "-----h",
  "-----o",
  "----ho",
  "----oo",
  "---ooo",
  "--Hooo",
  "--oooo",
  "-Hoooo",
  "-ooooo",
  "o--ooo",
  "oooooo",

  "o-----",
  "-----h",
  "-----o",
  "----ho",
  "----oo",
  "---ooo",
  "--o--o",
  "--oooo",
  "-o-ooo",
  "-ooooo",
  "o-o---",
  "ooo---",

  "o-----",
  null,
  "-----o",
  null,
  "----oo",
  "---ooo",
  null,
  "o----o",
  null,
  "-ooooo",
  null,
  "oooooo",
];

var variantNoteNames: { [key: string]: string } = {
  "C#": "Db",
  "D#": "Eb",
  "F#": "Gb",
  "G#": "Ab",
  "A#": "Bb",
};

function getFingerings(desiredNote: Note | NoNote, baseNote: Note | NoNote) {
  let currentNote = baseNote.letter || "D";
  let octave = baseNote.oct || 1;

  for (let index = 0; index < FINGERINGS.length; index++) {
    let nextWholeNote = String.fromCharCode(currentNote.charCodeAt(0) + 1);
    if (nextWholeNote === "H") nextWholeNote = "A";

    const fingering = `${FINGERINGS[index]}${index > 12 ? "+" : ""}`;

    let noteWithOctave = currentNote + octave;
    let altNoteWithOctave = variantNoteNames[currentNote]
      ? `${variantNoteNames[currentNote]}${octave}`
      : null;

    // console.log({ currentNote, altNoteWithOctave, fingering });

    if (
      desiredNote.name === noteWithOctave ||
      desiredNote.name === altNoteWithOctave
    )
      return fingering;

    if (currentNote.length !== 1 || ["E", "B"].includes(currentNote)) {
      currentNote = nextWholeNote;
    } else {
      currentNote = currentNote + "#";
    }

    if (currentNote === "C") octave++;
  }

  return null;
}
