import { useEffect, useState, memo } from "react";

import { useCountdown } from "hooks/useCountdown";
import { PlayNoteButton } from "components/PlayNoteButton/PlayNoteButton";
import { MusicNote } from "components/MusicNote/MusicNote";
import { NoteCard } from "components/NoteCard/NoteCard";
import { NoteSound } from "components/NoteSound/NoteSound";
import { useNoteDetectionHistory } from "hooks/useNoteDetectionHistory";
import { Flashcard, FlashcardType } from "types/Flashcard";
import { note as getNote, Note } from "@tonaljs/core";
import { InstrumentType } from "types/Flashcard";
const noteResetDuration = 240;

export const Flashcards = ({ flashcards }: { flashcards: Flashcard[] }) => {
  const [totalPoints, setTotalPoints] = useState(0);
  const [index, setIndex] = useState(0);
  const card = flashcards[index];
  const [noteDuration, setNoteDuration] = useState(0);
  const { count, reset: resetCountdown, set: setCountdown } = useCountdown(3);

  const { clearHistory } = useNoteDetectionHistory((history) => {
    const sum = history.reduce(
      (partialSum, a) =>
        partialSum + (a.note.name === card.note.name ? a.duration : 0),
      0
    );

    setNoteDuration(sum);

    if (noteDuration >= noteResetDuration) {
      showNextCard();
    }
  });

  const showAnswer = count <= 0;

  const showNextCard = () => {
    clearHistory();
    resetCountdown();
    setIndex(index + 1);
    setNoteDuration(0);

    if (!showAnswer) setTotalPoints(totalPoints + 1);
  };

  const instrumentData = {
    instrument: "accordion" as InstrumentType,
    note: getNote(card.note.name.replace("5", "4").replace("6", "5")) as Note,
  };

  return (
    <div
      style={{
        minWidth: 300,
      }}
    >
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: `${(100 / noteResetDuration) * noteDuration}vw`,
          height: 16,
          background: "#bada55",
          display: "block",
        }}
      ></div>
      {!showAnswer && (
        <div>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              borderBottom: "1px solid #888",
              paddingBottom: 5,
            }}
          >
            <span>⌛{count}</span>
            <span>{totalPoints}👓</span>
          </div>
          <div
            style={{
              minHeight: 140,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            {card?.type === FlashcardType.NoteName && <h1>{card.note.name}</h1>}
            {card?.type === FlashcardType.Sound && (
              <>
                <MemoNoteSound
                  note={instrumentData.note}
                  instrument={instrumentData.instrument}
                />
                <PlayNoteButton note={card.note} />
              </>
            )}
            {card?.type === FlashcardType.SheetMusicNote && (
              <MemoMusicNote note={card.note} flair={true} />
            )}
          </div>
          <button
            style={{ width: "100%" }}
            onClick={() => {
              setCountdown(0);
            }}
          >
            Reveal
          </button>
        </div>
      )}

      {showAnswer && (
        <div>
          <NoteCard note={card.note} />
          <MemoNoteSound
            note={instrumentData.note}
            instrument={instrumentData.instrument}
          />

          <button style={{ width: "100%" }} onClick={showNextCard}>
            Next
          </button>
        </div>
      )}
    </div>
  );
};

const MemoNoteSound = memo(NoteSound);
const MemoMusicNote = memo(MusicNote);
