import { Note } from "@tonaljs/core";
import { Tab } from "components/Tab/Tab";
import { PlayNoteButton } from "components/PlayNoteButton/PlayNoteButton";
import { MusicNote } from "components/MusicNote/MusicNote";

export const NoteCard = ({ note }: { note: Note }) => {
  return (
    <div
      style={{
        display: "flex",
        marginBottom: 10,
        minWidth: 300,
        justifyContent: "space-evenly",
      }}
    >
      <div>
        <h1>{note.name}</h1>
        <PlayNoteButton note={note} />
        <MusicNote note={note} />
      </div>
      <Tab style={{ width: 40, height: 40 * 7 }} note={note} />
    </div>
  );
};
