import * as Tone from "tone";
import create from "zustand";

Tone.context.debug = true;
const mic = new Tone.UserMedia();
const analyser = new Tone.Analyser({ type: "waveform" });
mic.connect(analyser);

type ToneState = {
  contextState: string;
  analyser: Tone.Analyser;
  audioDeviceId: string | undefined;
  //   increasePopulation: () => void;
  //   removeAllBears: () => void;
  start: () => void;
  changeAudioDevice: (id: string | undefined) => void;
};

export const useTone = create<ToneState>((set) => ({
  contextState: Tone.context.state,
  analyser,
  audioDeviceId: undefined,
  start: () => {
    if (Tone.context.state === "running") return;

    Tone.start();
    mic.open();

    Tone.context.on("statechange", (state: string) => {
      set({ contextState: state });
    });
  },
  changeAudioDevice: async (id) => {
    set({ audioDeviceId: id });
    await mic.open(id);
  },
  //   increasePopulation: () => set((state) => ({ bears: state.bears + 1 })),
  //   removeAllBears: () => set({ bears: 0 }),
}));
